import Vue from 'vue'
import Router from 'vue-router'

import Category from '@/pages/Category'
import Article from '@/pages/Article'
import User from '@/pages/User'
import Hello from '@/pages/Hello'



Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      component: Hello
    },{
      path: '/category',
      component: Category
    },{
      path: '/article',
      component: Article
    },{
      path: '/user',
      component: User
    }]
})












